<template>
  <div class="fusionEnd">
    <div class="fusionEnd-cont">
      <header>
        <div class="header-img-box">
          <div class="header-c">购买信息</div>
        </div>
        <div class="header-img-h">
          <img src="../assets/head/head.png" class="notice-img" />
        </div>
      </header>
      <div class="fusionEnd-conter">
        <div class="fusionEnd-conter-box">
          <cardModel :vname="vname" :CardInfo="CardInfo" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import cardModel from "../model/cardModel";
export default {
  data() {
    return {
      vname: "buyEnd",
      CardInfo: {},
    };
  },
  components: {
    cardModel,
  },
  mounted() {
    var CardInfo = JSON.parse(sessionStorage.getItem("objList"));

    if (CardInfo) {
      CardInfo.imgUrl = require("../assets/cardimg/" +
        CardInfo.imgUrlC +
        ".png");
      if (CardInfo.genre == "5") {
        CardInfo.cardType = "白卡";
      }
      if (CardInfo.genre == "4") {
        CardInfo.cardType = "蓝卡";
      }
      if (CardInfo.genre == "3") {
        CardInfo.cardType = "紫卡";
      }
      if (CardInfo.genre == "2") {
        CardInfo.cardType = "橙卡";
      }
      if (CardInfo.genre == "1") {
        CardInfo.cardType = "金卡";
      }
      if (CardInfo.page == "store") {
        this.CardInfo = CardInfo;
      } else {
        this.CardInfo = CardInfo;
      }
    }
  },
};
</script>
<style scoped>
.fusionEnd {
  width: 100%;
  height: 100%;
  position: relative;
}
.fusionEnd-cont {
  position: relative;
  max-width: 420px;
  height: 100%;
  margin: auto;
  background-color: #000;
}
header {
  width: 100%;
  height: 56px;
  background: linear-gradient(180deg, #13232e, #2d496f);
  position: relative;
}
.header-img-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}
.header-img-h {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.header-c {
  width: 100%;
  height: 56px;
  line-height: 60px;
  text-align: center;
  color: #faf5e4;
}
.notice-img {
  width: 100%;
  height: 100%;
  display: block;
}
.fusionEnd-conter {
  position: absolute;
  top: 57px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
}
.fusionEnd-conter-box {
  width: 100%;
  height: 100%;
  position: relative;
}
</style>
